
body,
html {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}
#app,
.full-height {
  height: 100%;
}
.map-card {
  width: 60%;
  max-width: 600px;
}
.info-content {
  max-width: 220px;
}





/* overwrite default */
.md-dialog {
  width: 60%;
  min-width: 310px;
}
.md-list-item .md-radio {
  display: block;
}
.md-list-item .md-radio-container {
  float: left;
}
.md-sidenav .md-sidenav-content {
  max-width: 80%;
}



